$templateCache.put("partials/about.html","<div class=\"row\">\n\n    <div class=\"inner cover about\">\n        <p class=\"lead\">\n            A passionate full stack Software Engineer based in <a href=\"https://instagram.com/jimbobbins\" target=\"_blank\">Manchester UK</a>.\n        </p>\n\n        <p>\n            I have been building and designing software for 10 years and have experience in all aspects of Software Development from larger\n            traditional organisations following a waterfall approach to having great experience in both Kanban and Scrum agile methodologies.\n        </p>\n\n        <p>\n            Originally coming from a background in Java/J2EE building enterprise message driven applications using technologies such as JMS, Apache\n            Camel, Spring and MySQL.\n        </p>\n\n        <p>\n            In recent years spending lots of time architecting and building modern JavaScript Web and Micro Service based applications in NodeJS,\n            AngularJS and MongoDB utilising tool kits such as StompJS, ui-router, Bootstrap, Lodash and Gulp amongst many more.\n        </p>\n\n        <p>\n            Approximately 2 years ago I became self employed and have become skilled in things such as Scala, Akka, Kafka and Neo4J along with various\n            other technologies. Leading small focus teams and providing expertise in required fields as well as designing and building reactive high\n            though put systems using Akka and more recently Akka-Streams.\n        </p>\n\n        <p>\n            To view some of my more recent work please head over to my <a href=\"http://github.com/jamesmorgan\" target=\"_blank\">GitHub account</a> or\n            take a look at my <a href=\"https://www.linkedin.com/in/jamesemorgan\" target=\"_blank\">LinkedIn profile</a> for examples of the companies\n            and project I have been working on.\n        </p>\n\n        <p>\n            If you would like get in touch please <a href=\"#/contact\">contact me</a> and I will reply as soon as I can.\n        </p>\n\n    </div>\n</div>\n\n<div class=\"row\">\n    <div class=\"github-card\" data-github=\"jamesmorgan\" data-width=\"400\" data-height=\"\" data-theme=\"default\"></div>\n    <script src=\"//cdn.jsdelivr.net/github-cards/latest/widget.js\"></script>\n</div>\n");
